tr {
  &.selected {
    td {
      background: #ffffcc;
    }
  }
}

td {
  &.checkbox {
    text-align: center;
  }
}

th {
  &.checkbox {
    text-align: center;
  }
}

[dir='rtl'] {
  td {
    &.checkbox {
      text-align: center;
    }
  }

  th {
    &.checkbox {
      text-align: center;
    }
  }
}
